<template>
  <div class="wrapper">
    <v-md-preview :text="title"></v-md-preview>
  </div>
  <div class="test-wrapper">
    <fx-button type="primary" @click="showTopModal">点击打开顶部对话框</fx-button>
    <fx-button type="primary" @click="showCenterModal">点击打开居中对话框</fx-button>
<!--    <div class="test-button button-width" @click="showTopModal">点击打开顶部对话框</div>-->
<!--    <div class="test-button button-width" @click="showCenterModal">点击打开居中对话框</div>-->
    <fx-modal :show="visible" :bottomShow="true" titleText="基础对话框" contentText="内容部分" :width="500" @close="closeModal"
              @confirm="confirm" :position="position"></fx-modal>
  </div>
  <div class="wrapper">
    <v-md-preview :text="base"></v-md-preview>
  </div>
  <div class="wrapper">
    <v-md-preview :text="self"></v-md-preview>
  </div>
  <div class="test-wrapper">
<!--    <div class="test-button button-width-one" @click="show">自定义对话框内容</div>-->
    <fx-button type="primary" @click="show">自定义对话框内容</fx-button>
    <fx-modal :show="state" :bottomShow="true"  :width="500" @close="close"
              @confirm="confirmClick">
      <template #title>
        <span>自定义标题</span>
        <button>插入按钮</button>
      </template>
      <template #content>
        <span>自定义内容,插入输入框</span>
        <input>
      </template>
      <template #bottom>
        <span>自定义底部</span>
        <button @click="close">取消</button>
      </template>
    </fx-modal>
  </div>
  <div class="wrapper">
    <v-md-preview :text="selfHtml"></v-md-preview>
  </div>
  <div class="wrapper">
    <v-md-preview :text="desc"></v-md-preview>
  </div>
  <div class="bottom"></div>

</template>

<script>
import {ref} from "vue"

export default {
  name: "aafxModal",
  setup() {
    // 基础使用
    const visible = ref(false);
    const position = ref('top')

    const showTopModal = () => {
      position.value = 'top'
      visible.value = true;
    };
    const showCenterModal = () => {
      position.value = 'center'
      visible.value = true;
    };

    const closeModal = () => {
      visible.value = false;
    };

    const confirm = () => {
      console.log('点击确定');
      visible.value = false;
    }

    // 插槽使用
    const state = ref(false)

    const show = () => {
      state.value = true
    }

    const close = () => {
      state.value = false
    }

    const confirmClick = () =>{
      console.log('点击确定');
      state.value = false;
    }

    const title = ref('# Modal 对话框\n' +
        '模态对话框。\n' +
        '## 何时使用\n' +
        '需要用户处理事务，又不希望跳转页面以致打断工作流程时，可以使用 Modal 在当前页面正中打开一个浮层，承载相应的操作。\n' +
        '## 基础用法\n' +
        '需要设置 show 属性，它接收 Boolean，当为 true 时显示 Modal。 Modal 分为三个部分：title , content 和 bottom, 底部 bottom 默认不显示,需要设置 bottomShow 属性，它接收 Boolean ,当它为 true 时显示底部区域。可用具名为 title， content， bottom的 slot来自定义标题，内容和底部。使用 width 属性定义对话框的宽度。\n'
        + 'titleText 属性定义标题文字, contentText 属性定义内容部分文字, position 属性定义对话框出现的位置\n'
        +'@confirm:确定事件, @close:关闭事件'
    )

    const base = ref('### 代码\n' +
        '``` html\n' +
        '<template>\n' +
        '    <fx-button type="primary" @click="showTopModal">点击打开顶部对话框</fx-button>\n' +
        '    <fx-button type="primary" @click="showCenterModal">点击打开居中对话框</fx-button>\n'+
        '    <fx-modal :show="visible" :bottomShow="true" titleText="基础对话框" \n'+
        '       contentText="内容部分" :width="500" @close="closeModal" @confirm="confirm" :position="position"></fx-modal>\n' +
        '</template>\n' +
        '\n' +
        '<script>\n' +
        'import {ref} from "vue"\n' +
        ' setup() {\n' +
        '    const visible = ref(false);\n' +
        '    const position = ref(\'top\')\n'+
        '\n' +
        '    const showTopModal = () => {\n' +
        '      position.value = \'top\'\n'+
        '      visible.value = true;\n' +
        '    };\n' +
        '    const showCenterModal = () => {\n' +
        '      position.value = \'center\'\n'+
        '      visible.value = true;\n' +
        '    };\n' +
        '\n' +
        '    const closeModal = () => {\n' +
        '      visible.value = false;\n' +
        '    };\n' +
        '\n' +
        '    const confirm = () =>{\n' +
        '      console.log(\'点击确定\');\n' +
        '      visible.value = false;\n' +
        '    }\n' +
        `return` + '{ visible,position,showModal,closeModal,confirm}\n' +
        '}\n' +
        '</sc' + 'ript>\n' +
        '```')

    const self = ref('## 自定义内容\n' +
        '可用title， content， bottom的具名插槽 slot，自定义标题，内容和底部。')

    const selfHtml = ref('### 代码\n' +
        '``` html\n' +
        '  <fx-button type="primary" @click="show">自定义对话框内容</fx-button>\n' +
        '  <fx-modal :show="state" :bottomShow="true"  :width="500" @close="close" @confirm="confirmClick">\n' +
        '      <template #title>\n' +
        '        <span>自定义标题</span>\n' +
        '        <button>插入按钮</button>\n' +
        '      </template>\n' +
        '      <template #content>\n' +
        '        <span>自定义内容,插入输入框</span>\n' +
        '        <input>\n' +
        '      </template>\n' +
        '      <template #bottom>\n' +
        '        <span>自定义底部</span>\n' +
        '        <button @click="close">取消</button>\n' +
        '      </template>\n' +
        '    </fx-modal>\n' +
        '  </div>\n' +
        '<script>\n' +
        'import {ref} from "vue"\n' +
        ' setup() {\n' +
        '  const state = ref(false)\n' +
        '\n' +
        '    const show = () => {\n' +
        '      state.value = true\n' +
        '    }\n' +
        '\n' +
        '    const close = () => {\n' +
        '      state.value = false\n' +
        '    }\n' +
        '\n' +
        '    const confirmClick = () =>{\n' +
        '      console.log(\'点击确定\');\n' +
        '      state.value = false;\n' +
        '    }\n' +
        '\n' +
        '  return '+'{state,show,close,confirmClick}\n' +
        '}\n' +
        '</sc'+'ript>\n'+
'```')

    const desc = ref('## Modal属性\n' +
        '|属性|说明|类型|可选值|\n' +
        '|-|-|-|-|\n' +
        '|show|是否展示对话框|boolean|true/false|\n' +
        '|bottomShow|是否展示底部区域|boolean|-|\n' +
        '|width|对话框宽度|number|-|\n' +
        '|position|对话框显示位置|string|top/center|\n' +
        '|titleText|标题文本|string|-|\n' +
        '|titleTextColor|标题文本颜色|string|-|\n' +
        '|titleBackground|标题区域背景颜色|string|-|\n' +
        '|contentText|内容区域文本|string|-|\n' +
        '|contentTextColor|内容区域文本颜色|string|-|\n' +
        '|confirmText|确认按钮文本|string|-|\n' +
        '|confirmBackground|确认按钮背景颜色|string|-|\n' +
        '|cancelText|取消按钮文本|string|-|\n' +
        '|cancelBackground|取消按钮背景颜色|string|-|\n' +
        '|borderRadius|圆角边框|number|-|\n' +
        '|borderBottomColor|内边框颜色|string|-|\n'+
        '## 插槽\n' +
        '|插槽名|说明|\n' +
        '|-|-|\n' +
        '|title|Modal标题区的内容|\n' +
        '|content|Modal中间区的内容|\n' +
        '|bottom|Modal底部区的内容|\n' +
        '## 事件\n' +
        '|事件名|说明|\n' +
        '|-|-|\n' +
        '|confirm|点击确定回调|\n' +
        '|close|点击关闭回调|'
    )
    return {
      visible,
      position,
      showTopModal,
      showCenterModal,
      closeModal,
      confirm,
      state,show,close,confirmClick,
      title, base, self,selfHtml,desc,
    }
  }
}
</script>

